import {
    number_format
} from '../../components/my-components/formater.js'
export default {
    methods: {
        clickGood(item) {
            this.$emit('click', {
                type: 'good',
                detail: {
                    val: item
                }
            })
        },
        formatePrice(val) {
            return number_format(val, 2, '.', ',')
        }
    },
    props: {
        goodsList: {
            type: Array,
            default () {
                return [{
                    img: '/static/img/global/tmp.png',
                    goodName: '',
                    color: '',
                    size: '',
                    num: 0,
                    price: '',
                    specifications: 'single', //单规格，multi多规格
                }]
            }
        }
    }, 
}
